.accordion__body {
    display: block;

    -webkit-animation: fadein .35s ease-in;

            animation: fadein .35s ease-in;

    outline: none;
}

.accordion__body--hidden {
    display: none;

    -webkit-animation: fadein .35s ease-in;

            animation: fadein .35s ease-in;

    opacity: 0;
    outline: none;
}

@keyframes fadein {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@-webkit-keyframes fadein {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}



